$primary-color-dark:   #E64A19;
$primary-color:        #FF5722;
$primary-color-light:  #FFCCBC;
$primary-color-text:   #FFFFFF;
$accent-color:         #CDDC39;
$primary-text-color:   #212121;
$secondary-text-color: #757575;
$divider-color:        #BDBDBD;

/* MUICSS */
@import "../../../node_modules/muicss/lib/sass/mui/colors";

$mui-primary-color:       $primary-color;
$mui-primary-color-dark:  $primary-color-dark;
$mui-primary-color-light: $primary-color-light;

$mui-accent-color:        $accent-color;
$mui-accent-color-dark:   mui-color('green', 'A100');
$mui-accent-color-light:  mui-color('green', 'A400');


@import "../../../node_modules/muicss/lib/sass/mui";

/* Material Design Icons */
$mdi-font-path: '../fonts/mdi';
@import "../../../node_modules/mdi/scss/materialdesignicons";

body, html {
  height: 100%;
  background-color: #eee;
  font-family: "Helvetica Neue",Helvetica,Arial,Verdana,Tahoma;
}

#header {
  right: 0;
  left: 0;
  transition: left .2s;
}

#header, #sidedrawer {
  position: fixed;
  top: 0;
  z-index: 2;
}

#appbar, #footer {
  background-color: $primary-color;
}

.appbar-brand {
  font-size: 20px;
  color: #fff;
  font-weight: bolder;
}

.space-top+h1 {
  color: rgba(0,0,0,.54);
}

#header.active, .header-shadow {
  box-shadow: 0 2px 5px rgba(0,0,0,.26);
  transition: box-shadow .2s;
}